.picture-projects {  
  // overflow: hidden;
  background: linear-gradient(180deg, rgba(0,0,0,1) 84%, rgba(22,22,22,0) 100%);
  padding: 5rem 2rem;  
  min-height: 100vh;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  
  iframe {
    position: static !important;
  }

  h1 {
    text-align: center;
    font-size: $title;
  }  
}